.logo{
  display: block;
  width: 136px;
  flex-shrink: 0;
  img{
    width: 100%;
    display: block;
  }
}


.header{
  padding: 30px 0;
  position: relative;
  z-index: 2;
  &__inner{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__lang{
    flex-shrink: 0;
  }
  &__btn{
    width: 183px;
    height: 53px;
    flex-shrink: 0;
  }
}

.nav{
  display: flex;
  align-items: center;
  &__link{
    font-size: 14px;
    line-height: 150%;
    &:not(:last-child){
      margin-right: 40px;
    }
  }
}

.lang{
  display: flex;
  align-items: center;
  input{
    position: absolute;
    opacity: 0;
    pointer-events: none;
    &:checked{
      &+label{
        &:before{
          left: 12px;
        }
      }
      &~.lang__first{
        color: inherit;
      }
      &~.lang__second{
        color: $primary;
      }
    }
  }
  label{
    background: rgba(255, 255, 255, 0.2);
    border-radius: 40px;
    width: 28px;
    height: 16px;
    flex-shrink: 0;
    margin: 0 8px;
    position: relative;
    &:before{
      content: '';
      position: absolute;
      width: 12px;
      height: 12px;
      background-color: $primary;
      left: 2px;
      top: 2px;
      border-radius: 50%;
      transition: all .3s;
    }
  }
  &__item{
    font-size: 14px;
    line-height: 150%;
    text-transform: uppercase;
    transition: all .3s;
  }
  &__first{
    order: -1;
    color: $primary;
  }
}
