.object{
  padding: 35px 0 70px;
  overflow: hidden;
  &__nav{
    display: flex;
  }
  &__back{
    display: flex;
    align-items: center;
    color: #fff;
    opacity: 0.5;
    svg{
      margin-right: 10px;
    }
    &:hover{
      opacity: 1;
      color: #fff;
      g{
        opacity: 1;
      }
    }
  }
  &__top{
    display: flex;
    justify-content: space-between;
    margin-top: 70px;
  }
  &__desc{
    font-size: 22px;
    line-height: 140%;
    margin-top: 16px;
  }
  &__slider{
    margin-top: 53px;
    position: relative;
    .swiper{
      &-container{
        max-width: 770px;
        margin: 0 auto;
      }
      &-slide{
        position: relative;
        padding-bottom: 58.5%;
        border-radius: 10px;
        overflow: hidden;
        &:not(.swiper-slide-active){
          &:before{
            content: '';
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            background: linear-gradient(0deg, rgba(0, 0, 0, 0.2), rgba(0, 0, 0, 0.2));
            z-index: 2;
            transition: all .3s;
          }
        }
        img{
          position: absolute;
          top: 0;
          left: 0;
          width: 100%;
          height: 100%;
          object-fit: cover;
        }
      }
      &-button{
        &-prev{
          left: 0;
        }
        &-next{
          right: 0;
        }
      }
    }
  }
  &__info{
    display: flex;
    justify-content: space-between;
    margin-top: 140px;
    &-desc{
      max-width: 436px;
      font-size: 20px;
      line-height: 150%;
    }
    &-right{
      width: 570px;
      max-width: 100%;
    }
    &-items{
      display: grid;
      grid-template-columns: 1fr 1fr;
      gap: 35px 30px;
    }
  }
}


.advantages{
  margin-top: 140px;
  padding: 73px 100px 80px;
  background: #222222;
  border-radius: 10px;
  &__list{
    margin-top: 70px;
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 15px;
    li{
      line-height: inherit;
      display: flex;
      font-weight: 500;
      &:before{
        content: '';
        background-image: url(../img/check.svg);
        background-repeat: no-repeat;
        background-position: center;
        width: 24px;
        height: 24px;
        flex-shrink: 0;
        margin-top: -1px;
        margin-right: 18px;
      }
    }
  }
}

.location{
  margin-top: 140px;
  &__items{
    margin-top: 50px;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    gap: 30px;
  }
  &__time{
    font-weight: 600;
    font-size: 20px;
    line-height: 130%;
    margin-bottom: 20px;
  }
}

.consult{
  margin-top: 140px;
  &__grid{
    margin-top: 50px;
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 30px;
  }
  &__desc{
    max-width: 515px;
    padding-bottom: 16px;
  }
  &__form,
  &__info{
    display: grid;
    grid-template-columns: 100%;
    gap: 30px;
  }
}

.scheme--object{
  padding: 70px 0;
}
