.main{
  background-image: url(../img/main-bg.webp);
  background-size: cover;
  background-position: center;
  margin-top: -113px;
  overflow: hidden;
  &__inner{
    padding: 160px 0 100px;
    position: relative;
  }
  &__title{
    max-width: 572px;
    color: $primary;
    span{
      color: #fff;
      padding-left: 80px;
      display: inline-block;
    }
  }
  &__info{
    margin-top: 60px;
    display: flex;
    align-items: center;
    &-item{
      display: flex;
      align-items: center;
      &:not(:last-child){
        margin-right: 40px;
      }
    }
    &-num{
      font-size: 40px;
      line-height: 120%;
      font-family: $font2;
      margin-right: 24px;
      opacity: 0.4;
    }
  }
  &__bottom{
    margin-top: 72px;
    display: flex;
    align-items: center;
  }
  &__desc{
    margin-left: 25px;
    max-width: 325px;
  }
  &__img{
    position: absolute;
    right: 40px;
    bottom: 0;
    width: 372px;
    &:before{
      content: '';
      position: absolute;
      bottom: -18px;
      left: 50%;
      transform: translateX(-50%);
      width: 357px;
      height: 357px;
      filter: blur(142px);
      background-color: $primary;
      border-radius: 0;
      z-index: 2;
    }
    &-man{
      display: block;
      width: 100%;
      position: relative;
      z-index: 3;
    }
    &-rounds{
      position: absolute;
      bottom: -150px;
      left: 50%;
      transform: translateX(-50%);
      width: 621px;
    }
  }
}

.scheme{
  &__inner{
    display: grid;
    grid-template-columns: calc(100% - 186px) 186px;
  }
  &__img{
    img{
      width: 100%;
      display: block;
    }
  }
  &__content{
    display: flex;
    align-items: center;
  }
  &__info{
    background: #2C2C2C;
    border-radius: 10px;
    width: 100%;
    padding: 30px 40px;
  }
  &__item{
    padding: 15px;
    &:not(:first-child){
      margin-top: 9px;
      border-top: 1px solid rgba(#fff,.1);
    }
  }
}

.map{
  margin: 70px 0;
  position: relative;
  &__img{
    img{
      width: 100%;
      display: block;
    }
  }
  &__content{
    position: absolute;
    width: 100%;
    padding: 128px 0;
  }
  &__title{
    max-width: 390px;
    font-size: 52px;
    span{
      color: #fff;
      padding-left: 85px;
      display: inline-block;
    }
  }
}
