@import "../vars";
.mbtn {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all ease-out .225s;
    padding: 0;
    &:focus {
        box-shadow: none;
    }
}

.mbtn-secondary{
    background: rgba(255, 255, 255, 0.1);
    backdrop-filter: blur(2px);
    border-radius: 6px;
    color: #FFFFFF;
    font-weight: 700;
    font-size: 14px;
    line-height: 150%;
    &:hover{
        background: rgba(255, 255, 255, 0.3);
        color: #FFFFFF;
    }
}

.mbtn-accent{
    border-radius: 6px;
    height: 60px;
    padding: 0 40px;
    font-weight: 700;
    font-size: 14px;
    background-color: $primary;
    color: #fff;
    &:hover{
        background-color: darken($primary,10%);
        color: #fff;
    }
}

.social{
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: 60px;
    padding: 0 30px 0 35px;
    background: #FFFFFF;
    backdrop-filter: blur(2px);
    border-radius: 10px;
    color: $black;
    font-weight: 700;
    font-size: 14px;
    &:hover{
        background: rgba(255,255,255,.1);
    }
}
