.display1{
  font-size: 42px;
  line-height: 120%;
  font-family: $font2;
}

.accent-color{
  color: $primary;
}

.semi-bold{
  font-weight: 600;
}

.small-text{
  font-size: 14px;
  line-height: 140%;
}
.medium-text{
  font-size: 16px;
  line-height: 150%;
}
.large-text{
  font-size: 18px;
  line-height: 150%;
}

.body-text{
  p{
    line-height: inherit;
    &:not(:last-child){
      margin-bottom: 10px;
    }
  }

}

.mlist{
  li{
    line-height: inherit;
    display: flex;
    &::before{
      content: '';
      display: block;
      width: 4px;
      height: 4px;
      border-radius: 50%;
      background-color: #000;
      transform: translateY(8px);
      margin-right: 5px;
      flex-shrink: 0;
    }
    &:not(:last-child){
      margin-bottom: 16px;
    }
  }
}
